<template>
  <v-container fluid>
    <v-app id="inspire">
      <v-card class="">
        <v-app-bar dense>
          <v-breadcrumbs :items="breadcrumbs"></v-breadcrumbs>
        </v-app-bar>
      </v-card>

      <v-card class="mt-5" outlined="">
        <v-toolbar flat class="blue-grey lighten-5">
          <v-toolbar-title>{{ tituloBar }}</v-toolbar-title>
        </v-toolbar>

        <v-container fluid>
          <v-form>
            <v-row>
              <v-col md="8">
                <v-text-field label="Descrio"></v-text-field>
              </v-col>

              <v-col cols="4">
                <v-text-field label="Valor R$"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
        <v-toolbar flat class="blue-grey lighten-4 bt-1">
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="salvar()">
            Salvar
          </v-btn>
        </v-toolbar>
      </v-card>
    </v-app>
  </v-container>
</template>

<script>
export default {
  name: 'ProdutoNovo',
  data: () => ({
    tituloBar: 'Novo Produto',
    qtdVezes: 0,
    horario: null,
    breadcrumbs: [
      {
        text: 'Incio',
        disabled: true,
      },
      {
        text: 'Produtos',
        disabled: false,
        href: '/#/produtos',
      },
      {
        text: 'Novo',
        disabled: true,
      },
    ],
    isLoading: false,
    items: [],
    model: null,
    search: null,
    tab: null,
    date: new Date().toISOString().substr(0, 10),
    dateFormatted: null,
    menu1: false,
    menu2: false,
    picker: null,
    e7: null,
    ex7: null,
    situacoes: ['Pendente', 'Parcial', 'Finalizada'],
  }),
  computed: {
    computedDateFormatted() {
      return this.formatDate(this.date)
    },
  },
  watch: {
    date() {
      this.dateFormatted = this.formatDate(this.date)
    },
    model(val) {
      if (val != null) this.tab = 0
      else this.tab = null
    },
    search() {
      // Items have already been loaded
      if (this.items.length > 0) return

      this.isLoading = true

      // Lazily load input items
      fetch('https://api.coingecko.com/api/v3/coins/list')
        .then((res) => res.clone().json())
        .then((res) => {
          this.items = res
        })
        .catch((err) => {
          console.log(err)
        })
        .finally(() => (this.isLoading = false))
    },
  },
  methods: {
    formatDate(date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate(date) {
      if (!date) return null

      const [month, day, year] = date.split('/')
      return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
    },
  },
}
</script>
