<template>
  <div>
    <v-card class="mx-auto filtros" outlined="">
      <v-toolbar flat color="blue darken-1" dark>
        <v-toolbar-title>Tramitao</v-toolbar-title>
      </v-toolbar>

      <form>
        <v-container fluid>
          <v-row>
            <v-col cols="4">
              <v-dialog
                ref="dialog"
                v-model="modal"
                :return-value.sync="date"
                persistent
                width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date"
                    label="Data"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker v-model="date" scrollable>
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="modal = false"
                    >Cancel</v-btn
                  >
                  <v-btn text color="primary" @click="$refs.dialog.save(date)"
                    >OK</v-btn
                  >
                </v-date-picker>
              </v-dialog>
            </v-col>
            <v-col cols="8">
              <v-combobox
                v-model="documento.categoria"
                :items="destinos"
                label="Destino"
              ></v-combobox>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-textarea solo name="input-7-4" label="Mensagem"></v-textarea>
            </v-col>
          </v-row>
          <v-row class="grey lighten-3">
            <v-col col="12" class="text-right">
              <v-btn
                class="ma-2"
                :loading="loading"
                :disabled="loading"
                color="success"
                @click="loader = 'loading'"
                right=""
              >
                Enviar
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </form>
    </v-card>
  </div>
</template>
<script>
export default {
  name: 'DocumentoAbaTramitacaoNova',
  data: () => ({
    documento: {},
    loader: null,
    loading: false,
    date: new Date().toISOString().substr(0, 10),
    modal: null,
    destinos: [
      'Secretaria',
      'Plenrio',
      'Mesa diretora',
      'Arquivo',
      'Jurdico',
      'Gabinete X',
      'Gabinete Y',
    ],
  }),
  watch: {
    loader() {
      const l = this.loader
      this[l] = !this[l]

      setTimeout(() => (this[l] = false), 3000)

      this.loader = null
    },
  },
}
</script>
