<template>
  <v-container class="" fluid>
    <v-row class="p0 m0">
      <v-col md="3">
        <v-card class="mx-auto" max-width="300" tile>
          <v-list>
            <v-list-item-group v-model="item" color="primary">
              <v-list-item
                v-for="(item, i) in submenus"
                :key="i"
                :disabled="item.disabled"
                :class="{ 'v-list-item--active': item.active }"
                @click="irPara(item.route)"
              >
                <v-list-item-content>
                  <v-list-item-title v-text="item.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>

      <v-col md="9">
        <router-view></router-view>
        
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { bus } from '../../../main'

export default {
  name: 'PacientesBase',
  data: () => ({
    tituloBar: 'Cadastrar novo paciente',
    disabled: true,
    item: {},
    
  }),
  created() {
    this.$router.push({ name: 'PacienteNovo' })

    bus.$on('paciente:salvar', () => {
      this.disabled = false
      this.submenus[0].text = 'Detalhes'
      
    })
  },
  computed: {
    submenus() {
      return [
        { text: 'Cadastro', disabled: false, route: 'PacienteNovo' },
        {
          text: 'Observaes',
          disabled: this.disabled,
          route: 'PacienteObservacao',
        },
        { text: 'Histrico', disabled: this.disabled, route: 'PacienteHistorico'  },
        { text: 'Anamnese', disabled: this.disabled, route: 'PacienteAnamnese'  },
        { text: 'Financeiro', disabled: this.disabled, route: 'PacienteFinanceiro'  },
        { text: 'Sesses', disabled: this.disabled, route: 'PacienteSessoes'  },
        { text: 'Produtos', disabled: this.disabled, route: 'PacienteProdutos'  },
      ]
    },
  },
  methods: {
    irPara(url) {
      console.log(url)
      this.$router.push({ name: url })
    },
    salvar() {
      this.disabled = false
      this.submenus[0].text = 'Detalhes'
      this.tituloBar = 'Detalhes do paciente'
      this.breadcrumbs[2].text = 'Detalhes'
    },
  },
}
</script>
