<template>
  <v-container fluid>
    <v-app id="inspire">
      <v-card class="">
        <v-app-bar dense>
          <v-breadcrumbs :items="breadcrumbs"></v-breadcrumbs>

          <v-spacer></v-spacer>

          <v-btn small color="primary" @click="novo()"
            >Novo <v-icon small>mdi-plus</v-icon></v-btn
          >
        </v-app-bar>
      </v-card>

      <v-card class="mt-5" outlined="">
        <v-toolbar flat class="blue-grey lighten-5">
          <v-toolbar-title>Sesses</v-toolbar-title>
        </v-toolbar>

        <v-container fluid>
          <v-row class="fill-height">
            <v-col>
              <v-sheet height="64">
                <v-toolbar flat color="white">
                  <v-btn
                    outlined
                    class="mr-4"
                    color="grey darken-2"
                    @click="setToday"
                  >
                    Today
                  </v-btn>
                  <v-btn fab text small color="grey darken-2" @click="prev">
                    <v-icon small>mdi-chevron-left</v-icon>
                  </v-btn>
                  <v-btn fab text small color="grey darken-2" @click="next">
                    <v-icon small>mdi-chevron-right</v-icon>
                  </v-btn>
                  <v-toolbar-title v-if="$refs.calendar">
                    {{ $refs.calendar.title }}
                  </v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-menu bottom right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        outlined
                        color="grey darken-2"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <span>{{ typeToLabel[type] }}</span>
                        <v-icon right>mdi-menu-down</v-icon>
                      </v-btn>
                    </template>
                    <v-list>
                      <v-list-item @click="type = 'day'">
                        <v-list-item-title>Day</v-list-item-title>
                      </v-list-item>
                      <v-list-item @click="type = 'week'">
                        <v-list-item-title>Week</v-list-item-title>
                      </v-list-item>
                      <v-list-item @click="type = 'month'">
                        <v-list-item-title>Month</v-list-item-title>
                      </v-list-item>
                      <v-list-item @click="type = '4day'">
                        <v-list-item-title>4 days</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </v-toolbar>
              </v-sheet>
              <v-sheet height="600">
                <v-calendar
                  locale="pt-BR"
                  ref="calendar"
                  v-model="value"
                  color="primary"
                  :events="events"
                  :event-color="getEventColor"
                  :type="type"
                  @click:event="showEvent"
                  @click:more="viewDay"
                  @click:date="viewDay"
                  @change="updateRange"
                >
                  <template #day-body="{ date, week }">
                    <div
                      class="v-current-time"
                      :class="{ first: date === week[0].date }"
                      :style="{ top: nowY }"
                    ></div>
                  </template>
                </v-calendar>
                <v-menu
                  v-model="selectedOpen"
                  :close-on-content-click="false"
                  :activator="selectedElement"
                  offset-x
                >
                  <v-card color="grey lighten-4" min-width="350px" flat>
                    <v-toolbar :color="selectedEvent.color" dark>
                      <v-btn icon>
                        <v-icon>mdi-pencil</v-icon>
                      </v-btn>
                      <v-toolbar-title
                        v-html="selectedEvent.name"
                      ></v-toolbar-title>
                      <v-spacer></v-spacer>
                      <v-btn icon>
                        <v-icon>mdi-heart</v-icon>
                      </v-btn>
                      <v-btn icon>
                        <v-icon>mdi-dots-vertical</v-icon>
                      </v-btn>
                    </v-toolbar>
                    <v-card-text>
                      <span v-html="selectedEvent.details"></span>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn
                        text
                        color="secondary"
                        @click="selectedOpen = false"
                      >
                        Cancel
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-menu>
              </v-sheet>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-app>
  </v-container>
</template>

<script>
export default {
  name: 'SessoesCalendario',
  data: () => ({
    breadcrumbs: [
      {
        text: 'Incio',
        disabled: true,
      },
      {
        text: 'Sesses',
        disabled: false,
        href: '/#/sessoes',
      },
      {
        text: 'Calendrio',
        disabled: true,
      },
    ],
    focus: '',
    type: '4day',
    typeToLabel: {
      month: 'Ms',
      week: 'Semanal',
      day: 'Dirio',
      '4day': '4 Dias',
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    colors: ['blue'],
    value: '',
    ready: false,
  }),
  mounted() {
    this.$refs.calendar.checkChange(), (this.ready = true)
    this.scrollToTime()
    this.updateTime()
  },
  computed: {
    cal() {
      return this.ready ? this.$refs.calendar : null
    },
    nowY() {
      return this.cal ? this.cal.timeToY(this.cal.times.now) + 'px' : '-10px'
    },
  },
  methods: {
    viewDay({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor(event) {
      return event.color
    },
    setToday() {
      this.focus = ''
    },
    prev() {
      this.$refs.calendar.prev()
    },
    next() {
      this.$refs.calendar.next()
    },
    showEvent({ nativeEvent, event }) {
      console.log(event)
      //   const open = () => {
      //     this.selectedEvent = event
      //     this.selectedElement = nativeEvent.target
      //     setTimeout(() => (this.selectedOpen = true), 10)
      //   }

      //   if (this.selectedOpen) {
      //     this.selectedOpen = false
      //     setTimeout(open, 10)
      //   } else {
      //     open()
      //   }

      nativeEvent.stopPropagation()
    },
    updateRange() {
      const events = []

      events.push({
        name: 'Pedro',
        start: new Date('2020-09-21T07:15:00'),
        end: new Date('2020-09-21T08:15:00'),
        color: this.colors[this.rnd(0, this.colors.length - 1)],
        timed: true,
        details: 'Teste aqui vamos ver',
      })
      events.push({
        name: 'Maria',
        start: new Date('2020-09-21T08:20:00'),
        end: new Date('2020-09-21T09:20:00'),
        color: this.colors[this.rnd(0, this.colors.length - 1)],
        timed: true,
      })
      events.push({
        name: 'Joaquim',
        start: new Date('2020-09-22T18:00:00'),
        end: new Date('2020-09-22T19:00:00'),
        color: this.colors[this.rnd(0, this.colors.length - 1)],
        timed: true,
      })

      this.events = events
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a
    },
    getCurrentTime() {
      return this.cal
        ? this.cal.times.now.hour * 60 + this.cal.times.now.minute
        : 0
    },
    scrollToTime() {
      const time = this.getCurrentTime()
      const first = Math.max(0, time - (time % 30) - 30)

      this.cal.scrollToTime(first)
    },
    updateTime() {
      setInterval(() => this.cal.updateTimes(), 60 * 1000)
    },
    novo() {
      this.$router.push({ name: 'SessoesNova' })
    },
  },
}
</script>

<style lang="scss">
.v-current-time {
  height: 2px;
  background-color: #ea4335;
  position: absolute;
  left: -1px;
  right: 0;
  pointer-events: none;

  &.first::before {
    content: '';
    position: absolute;
    background-color: #ea4335;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-top: -5px;
    margin-left: -6.5px;
  }
}
</style>
