<template>
  <v-container fluid>
    <v-app id="inspire">
      <v-card class="">
        <v-app-bar dense>
          <v-breadcrumbs :items="breadcrumbs"></v-breadcrumbs>
        </v-app-bar>
      </v-card>

      <v-card class="mt-5" outlined="">
        <v-toolbar flat class="blue-grey lighten-5">
          <v-toolbar-title>{{ tituloBar }}</v-toolbar-title>
        </v-toolbar>

        <v-container fluid>
          <v-form>
            <v-row>
              <v-col md="12">
                <v-autocomplete
                  v-model="model"
                  :items="items"
                  :loading="isLoading"
                  :search-input.sync="search"
                  chips
                  clearable
                  hide-details
                  hide-selected
                  item-text="name"
                  item-value="symbol"
                  label="Paciente"
                >
                  <template v-slot:no-data>
                    <v-list-item>
                      <v-list-item-title>
                        <strong>informe parte do nome ou cpf</strong>
                      </v-list-item-title>
                    </v-list-item>
                  </template>
                  <template v-slot:selection="{ attr, on, item, selected }">
                    <v-chip
                      v-bind="attr"
                      :input-value="selected"
                      color="blue-grey"
                      class="white--text"
                      v-on="on"
                    >
                      <v-icon left>mdi-coin</v-icon>
                      <span v-text="item.name"></span>
                    </v-chip>
                  </template>
                  <template v-slot:item="{ item }">
                    <v-list-item-avatar
                      color="indigo"
                      class="headline font-weight-light white--text"
                    >
                      {{ item.name.charAt(0) }}
                    </v-list-item-avatar>
                    <v-list-item-content>
                      <v-list-item-title v-text="item.name"></v-list-item-title>
                      <v-list-item-subtitle
                        v-text="item.symbol"
                      ></v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-action>
                      <v-icon>mdi-coin</v-icon>
                    </v-list-item-action>
                  </template>
                </v-autocomplete>
                <template v-slot:extension>
                  <v-tabs
                    v-model="tab"
                    :hide-slider="!model"
                    color="blue-grey"
                    slider-color="blue-grey"
                  >
                    <v-tab :disabled="!model">News</v-tab>
                    <v-tab :disabled="!model">Trading</v-tab>
                    <v-tab :disabled="!model">Blog</v-tab>
                  </v-tabs>
                </template>
              </v-col>
            </v-row>

            <v-row> </v-row>

            <v-row>
              <v-col md="2">
                <v-menu
                  ref="menu1"
                  v-model="menu1"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="dateFormatted"
                      label="Data"
                      hint=""
                      persistent-hint
                      v-bind="attrs"
                      @blur="date = parseDate(dateFormatted)"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="date"
                    no-title
                    @input="menu1 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col md="2">
                  <v-text-field
                  v-model="horario"
                  label="Hora"
                ></v-text-field>
                <!-- <v-time-picker
                  v-model="e7"
                  format="24hr"
                  :landscape="$vuetify.breakpoint.smAndUp"
                ></v-time-picker> -->
              </v-col>
              <v-col cols="2">
                <v-text-field
                  v-model="qtdVezes"
                  label="Repete quantas vezes"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-radio-group v-model="ex7" row>
                  <v-radio
                    label="Dirio"
                    color="red darken-3"
                    value="red darken-3"
                  ></v-radio>
                  <v-radio
                    label="Semanal"
                    color="indigo"
                    value="indigo"
                  ></v-radio>
                  <v-radio
                    label="Quinzenal"
                    color="indigo darken-3"
                    value="indigo darken-3"
                  ></v-radio>
                  <v-radio
                    label="Mensal"
                    color="orange"
                    value="orange"
                  ></v-radio>
                </v-radio-group>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
        <v-toolbar flat class="blue-grey lighten-4 bt-1">
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="salvar()">
            Salvar
          </v-btn>
        </v-toolbar>
      </v-card>
    </v-app>
  </v-container>
</template>

<script>
export default {
  name: 'SessaoNova',
  data: () => ({
    tituloBar: 'Nova Sesso',
    qtdVezes: 0,
    horario: null,
    breadcrumbs: [
      {
        text: 'Incio',
        disabled: true,
      },
      {
        text: 'Sesses',
        disabled: false,
        href: '/#/sessoes',
      },
      {
        text: 'Cadastro',
        disabled: true,
      },
    ],
    isLoading: false,
    items: [],
    model: null,
    search: null,
    tab: null,
    date: new Date().toISOString().substr(0, 10),
    dateFormatted: null,
    menu1: false,
    menu2: false,
    picker: null,
    e7: null,
    ex7: null,
  }),
  computed: {
    computedDateFormatted() {
      return this.formatDate(this.date)
    },
  },
  watch: {
    date() {
      this.dateFormatted = this.formatDate(this.date)
    },
    model(val) {
      if (val != null) this.tab = 0
      else this.tab = null
    },
    search() {
      // Items have already been loaded
      if (this.items.length > 0) return

      this.isLoading = true

      // Lazily load input items
      fetch('https://api.coingecko.com/api/v3/coins/list')
        .then((res) => res.clone().json())
        .then((res) => {
          this.items = res
        })
        .catch((err) => {
          console.log(err)
        })
        .finally(() => (this.isLoading = false))
    },
  },
  methods: {
    formatDate(date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate(date) {
      if (!date) return null

      const [month, day, year] = date.split('/')
      return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
    },
  },
}
</script>
