<template>
  <div>
    <v-breadcrumbs :items="itemsBreadcrumbs"></v-breadcrumbs>

    <v-card class="mx-auto filtros" outlined="">
      <v-toolbar flat color="indigo" dark>
        <v-toolbar-title>Nova Sesso</v-toolbar-title>
      </v-toolbar>

      <v-stepper v-model="e1" :vertical="vertical" :alt-labels="altLabels">
        <template v-if="vertical">
          <template v-for="n in steps">
            <v-stepper-step
              :key="`${n}-step`"
              :complete="e1 > n"
              :step="n"
              :editable="editable"
            >
              Passo {{ n }}
            </v-stepper-step>

            <v-stepper-content :key="`${n}-content`" :step="n">
              <v-card
                class="mb-12"
                color="grey lighten-1"
                height="200px"
              ></v-card>

              <v-btn color="primary" @click="nextStep(n)">
                Continue
              </v-btn>

              <v-btn text>Cancel</v-btn>
            </v-stepper-content>
          </template>
        </template>
        <template v-else>
          <v-stepper-header>
            <template v-for="n in steps">
              <v-stepper-step
                :key="`${n}-step`"
                :complete="e1 > n"
                :step="n"
                :editable="editable"
              >
                Passo {{ n }}
              </v-stepper-step>

              <v-divider v-if="n !== steps" :key="n"></v-divider>
            </template>
          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content :step="1">
              <v-row>
                <v-col cols="2">
                  <v-dialog
                    ref="dialog"
                    v-model="modal"
                    :return-value.sync="date"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="date"
                        label="Data"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="date" scrollable>
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="modal = false"
                        >Cancel</v-btn
                      >
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.dialog.save(date)"
                        >OK</v-btn
                      >
                    </v-date-picker>
                  </v-dialog>
                </v-col>
                <v-col cols="4">
                  <v-combobox
                    v-model="documento.tipo"
                    :items="tipos"
                    label="Tipo"
                  ></v-combobox>
                </v-col>
                <v-col>
                  <v-text-field
                    v-model="documento.linkTransmissao"
                    label="Link transmisso"
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="documento.titulo"
                    label="Ttulo"
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col col="12">
                  <v-textarea
                    v-model="documento.descricao"
                    label="Descrio"
                    value=""
                    hint=""
                  ></v-textarea>
                </v-col>
              </v-row>

              <v-btn color="primary" @click="nextStep(1)">
                Avanar
              </v-btn>

              <v-btn text>Cancelar</v-btn>
            </v-stepper-content>

            <v-stepper-content :step="2">
              <v-row>
                <v-col>
                  <v-file-input
                    small-chips
                    multiple
                    label="Anexos"
                  ></v-file-input>
                </v-col>
                <v-col>
                  <v-text-field
                    label="Ata"
                    required
                    title="Abrir modal com pesquisa de documentos"
                    hint="Abrir modal com pesquisa de documentos"
                  ></v-text-field>
                </v-col>
                <v-col>
                  <v-text-field
                    label="Pauta"
                    required
                    title="Abrir modal com pesquisa de documentos"
                    hint="Abrir modal com pesquisa de documentos"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col col="6">
                  <v-card>
                    <v-card-subtitle>Presenas</v-card-subtitle>
                  </v-card>
                </v-col>
                <v-col col="6">
                  <v-card>
                    <v-card-subtitle>Documentos e reas e Votaes</v-card-subtitle>
                  </v-card>
                </v-col>
              </v-row>

              <v-btn color="primary">
                Salvar
              </v-btn>

              <v-btn text>Cancelar</v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </template>
      </v-stepper>
    </v-card>
  </div>
</template>

<script>
// import Editor from '@tinymce/tinymce-vue'

export default {
  name: 'SessaoNova',
  components: {
    // Editor,
  },
  data: () => {
    return {
      e1: 1,
      steps: 2,
      vertical: false,
      altLabels: false,
      editable: false,
      dialog: false,
      loader: null,
      loading: false,
      itemsBreadcrumbs: [
        {
          text: 'Incio',
          disabled: false,
          href: '/#/caixa-entrada',
        },
        {
          text: 'Sesses',
          disabled: false,
          href: '/#/sessoes',
        },
        {
          text: 'Nova',
          disabled: true,
        },
      ],
      date: new Date().toISOString().substr(0, 10),
      documento: {},
      tipos: ['Extraordinria', 'Ordinria'],
    }
  },
  methods: {
    onInput(val) {
      this.steps = parseInt(val)
    },
    nextStep(n) {
      if (n === this.steps) {
        this.e1 = 1
      } else {
        this.e1 = n + 1
      }
    },
  },
  watch: {
    steps(val) {
      if (this.e1 > val) {
        this.e1 = val
      }
    },
    vertical() {
      this.e1 = 2
      requestAnimationFrame(() => (this.e1 = 1)) // Workarounds
    },
    loader() {
      const l = this.loader
      this[l] = !this[l]

      setTimeout(() => (this[l] = false), 3000)

      this.loader = null
    },
  },
}
</script>
